.gpt3__header{
    display: flex;

}
.gpt3__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt3__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}
.gpt3__header-content p{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}
.gpt3__header-content__input{
    width: 100%;
    margin:2rem 0 1rem;
    display: flex;
}
.gpt3__header-content__input input {
    background: var(--color-footer);
    color: #fff;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    min-height: 50px;
    width: 100%;
    padding: 0 1rem;
    border: 2px solid var(--color-footer);
    border-radius: 5px 0 0 5px;
    outline: none;
    flex: 2;
}

.gpt3__header-content__input button{
    flex:0.6;
    width: 100%;
    min-height: 50px;
    border-radius: 0 5px 5px 0;
    border: none;
    font-weight: bold;
    font-size: 20px;
    font-family: var(--font-family);
    line-height: 27px;
    background: #ff4820;
    color: #fff;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
}

.gpt3__header-content__input button:hover{
    background: #ff5e3a;
}

.gpt3__header-content__people{
    /*background: #81AFDD;*/
    width: 100%;
    display: flex;
    /*align-items: center;*/
    /*justify-content: flex-start;*/
    margin-top: 2rem;
}
.gpt3__header-content__people img{
    object-fit: contain;
    height: 38px;
}
.gpt3__header-content__people p{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: white;
    margin: 0 0 0 1rem;
    text-align: center;
}
.gpt3__header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gpt3__header-image img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

@media screen and (max-width: 1050px){
    .gpt3__header{
        flex-direction: column;
    }

    .gpt3__header-content{
        margin: 0 0 3rem;
    }

}
@media screen and (max-width: 650px){
    .gpt3__header h1{
        font-size: 48px;
        line-height: 60px;
    }
    .gpt3__header p{
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-content__people{
        flex-direction: column;
    }
    .gpt3__header-content__people p{
        margin: 0;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 16px;
        line-height: 24px;
    }
}
@media screen and (max-width: 490px){
    .gpt3__header h1{
        font-size: 36px;
        line-height: 48px;
    }
    .gpt3__header p{
        font-size: 14px;
        line-height: 24px;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 12px;
        line-height: 16px;
    }


}